import React, { useState } from 'react';
import en from './instructorsEn.json';
import fr from './instructorsFr.json';
import './instructors.css'

export default function Instructors() {
  const [isFrench, setIsFrench] = useState(false);

  const toggleLanguage = () => {
    setIsFrench((prevIsFrench) => !prevIsFrench);
  };

  const languageData = isFrench ? fr : en;
  return (
    <>
    
    <div>

      {/* Services Section */}
      <section id="services" className="py-5">
        <div className="container">
          <h2 className="display-4 text-center mb-5">{languageData.title}</h2>
          <div className="row">
            {/* Instructor 1 */}
            <div className="col-lg-6">
              <div className="card mb-4">
                <div className="row no-gutters align-items-center">
                  <div className="col-md-4 text-center">
                    <img className="card-img" src="assets/dude.jpeg" alt="Instructor 1 Max" />
                    <div className="gallery-container">
                      <a href="#" className="gallery-link" data-toggle="modal" data-target="#galleryModal1">
                        <img src="assets/gallery.png" alt="Media" className="gallery-icon" />
                      </a>
                      <div className="gallery-caption">{languageData.media}</div>
                    </div>
                  </div>
                  <div className="col-md-8">
                    <div className="card-body">
                      <h5 className="card-title text-left">Max</h5>
                      <p className="card-text text-left">{languageData.max.description}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Instructor 2 */}
            <div className="col-lg-6">
              <div className="card mb-4">
                <div className="row no-gutters align-items-center">
                  <div className="col-md-4 text-center">
                    <img className="card-img" src="assets/dude2.png" alt="Service 2" />
                    <div className="gallery-container">
                      <a href="#" className="gallery-link" data-toggle="modal" data-target="#galleryModal2">
                        <img src="assets/gallery.png" alt="Media" className="gallery-icon" />
                      </a>
                      <div className="gallery-caption">{languageData.media}</div>
                    </div>
                  </div>
                  <div className="col-md-8">
                    <div className="card-body">
                      <h5 className="card-title text-left">Chris</h5>
                      <p className="card-text text-left">{languageData.chris.description}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Gallery Modals */}
      <div className="modal fade" id="galleryModal1" tabIndex="-1" role="dialog" aria-labelledby="galleryModal1Label" aria-hidden="true">
        <div className="modal-dialog modal-dialog-centered modal-lg" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="galleryModal1Label">Highlights - Max</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              {/* Add your YouTube video here */}
              <iframe
                width="100%" height="315"
                src="https://www.youtube.com/embed/yRfrq9W1lhs"
                frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
          </div>
        </div>
      </div>

      <div className="modal fade" id="galleryModal2" tabIndex="-1" role="dialog" aria-labelledby="galleryModal1Label" aria-hidden="true">
        <div className="modal-dialog modal-dialog-centered modal-lg" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="galleryModal1Label">Highlights - Chris</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              {/* Add your YouTube video here */}
              <iframe
                width="100%" height="315"
                src="https://www.youtube.com/embed/XB45j761aJw"
                frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button
        className="btn btn-danger btn-lg"
        onClick={toggleLanguage}
        style={{ position: 'fixed', bottom: '20px', right: '20px' }}
      >
        {isFrench ? 'Switch to English' : 'Passer en Français'}
      </button>
      </>
  );
};


