import React, { useState } from 'react';
import './Leaderboards.css';

import logoImage from './assets/logo1.png';
import track1Image from './assets/track1.jpg';
import track2Image from './assets/track2.jpg';
import track3Image from './assets/track3.jpg';
import track4Image from './assets/track4.jpg';



const Leaderboards = () => {
  // Define the state for the year and data
  const [year, setYear] = useState('2023');

  // Define the data for both years
  const track1Data = {
    '2022': [
      { name: 'John Cena', car: 'Porsche 911 GT3 RS', time: '1:25.42' },
      { name: 'Michael Jordan', car: 'Mercedes C63s AMG', time: '1:28.95' },
      { name: 'Harry Potter', car: 'Audi RS5', time: '1:30.48' },
      { name: 'Matt Hardy', car: 'BMW M4 comp.', time: '1:34.98' },],
    '2023': [
      { name: 'Obi-Wan Kenobi', car: 'Ferrari 458 Italia', time: '1:30.36' },
          { name: 'Squilliam Fancypants', car: 'Lamborghini Huracán', time: '1:32.15' },
          { name: 'Troy Bolton', car: 'Aston Martin Vantage', time: '1:36.48' },
          { name: 'Dean Winchester', car: 'McLaren 720S', time: '1:38.77' }],
  };

  const track2Data = {
    '2022': [
      { name: 'Paul Walker', car: 'Nissan Nismo GT-R', time: '1:45.20' },
    { name: 'Roman Reigns', car: 'Audi R8', time: '1:50.15' },
    { name: 'Lionel Messi', car: 'Ferrari 488', time: '1:55.20' },
    { name: 'Roman Reigns', car: 'Audi R8', time: '1:50.15' }],
    '2023': [
      { name: 'Stewie Griffin', car: 'Bugatti Chiron', time: '1:20.36' },
      { name: 'Walter White', car: 'Lamborghini Aventador', time: '1:22.15' },
      { name: 'Speedy Gonzalez', car: 'Ferrari 812 Superfast', time: '1:26.48' },
      { name: 'Lester Green', car: 'Porsche 911 Turbo S', time: '1:28.77' }],
  };

  const track3Data = {
    '2022': [
      { name: 'Luke Skywalker', car: 'Bugatti Chiron', time: '1:20.36' },
    { name: 'Jack Sparrow', car: 'Lamborghini Aventador', time: '1:22.15' },
    { name: 'Charlie Bucket', car: 'Ferrari 812 Superfast', time: '1:26.48' },
    { name: 'Buzz Lightyear', car: 'Porsche 911 Turbo S', time: '1:28.77' }],
    '2023': [
      { name: 'Augustus Gloop', car: 'Porsche 911 GT3 RS', time: '1:30.36' },
          { name: 'Peter Parker', car: 'Mercedes C63s AMG', time: '1:32.15' },
          { name: 'Steve Jobs', car: 'Audi RS5', time: '1:36.48' },
          { name: 'Tom Cruise', car: 'BMW M4 comp.', time: '1:38.77' }],
  };

  const track4Data = {
    '2022': [
      { name: 'Kobe Bryant', car: 'Ferrari 458 Italia', time: '1:30.36' },
    { name: 'Bruce Banner', car: 'Lamborghini Huracán', time: '1:32.15' },
    { name: 'Jack Ryan', car: 'Aston Martin Vantage', time: '1:36.48' },
    { name: 'Indiana Jones', car: 'McLaren 720S', time: '1:38.77' }],
    '2023': [
      { name: 'Hasbulla Magomedov', car: 'Nissan Nismo GT-R', time: '1:45.20' },
      { name: 'Walt Disney', car: 'Audi R8', time: '1:50.15' },
      { name: 'Jesse Pinkman', car: 'Ferrari 488', time: '1:55.20' },
      { name: 'Saul Goodman', car: 'Audi R8', time: '1:50.15' }],
  };

  // Helper function to update the data based on the selected year
  const updateData = () => {
    // Update the data based on the selected year
    // Replace 'track1Data[year]' with the corresponding data for each track
    const track1 = track1Data[year];
    const track2 = track2Data[year];
    const track3 = track3Data[year];
    const track4 = track4Data[year];

    // Return the updated data for each track
    return { track1, track2, track3, track4 };
  };

  // Handle the year toggle button click
  const handleYearToggle = (selectedYear) => {
    setYear(selectedYear);
  };

  // Get the updated data based on the selected year
  const { track1, track2, track3, track4 } = updateData();

  return (
    <div className="container">
      <section id="leaderboards" className="py-5">
        <div className="text-center">
          <h2 className="display-4 text-center mb-5">Leaderboards</h2>
          <div className="btn-group mb-3" role="group" aria-label="Year Toggle">
            <button
              type="button"
              className={`btn ${year === '2022' ? 'btn-danger' : 'btn-secondary'}`}
              onClick={() => handleYearToggle('2022')}
            >
              2022
            </button>
            <button
              type="button"
              className={`btn ${year === '2023' ? 'btn-danger' : 'btn-secondary'}`}
              onClick={() => handleYearToggle('2023')}
            >
              2023
            </button>
          </div>
        </div>
        <div className="leaderboard-section">
          {/* Display track 1 data */}
          <div className="leaderboard-item">
            <div className="row">
              <div className="col-xl">
                <h3 className="text-left">Daisy Circuit</h3>
                <table className="table table-striped text-left">
                  <thead>
                    <tr>
                      <th>Driver Name</th>
                      <th>Car</th>
                      <th>Fastest Lap Time</th>
                    </tr>
                  </thead>
                  <tbody id="track1-table-body">
                    {track1.map((item, index) => (
                      <tr key={index}>
                        <td>{item.name}</td>
                        <td>{item.car}</td>
                        <td>{item.time}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <img src="assets/track1.jpg" alt="Daisy Circuit" />
            </div>
          </div>

          {/* Display track 2 data */}
          <div className="leaderboard-item">
            <div className="row">
              <img src="assets/track2.jpg" alt="Royal Raceway" />
              <div className="col-xl">
                <h3 className="text-left">Royal Raceway</h3>
                <table className="table table-striped text-left">
                  <thead>
                    <tr>
                      <th>Driver Name</th>
                      <th>Car</th>
                      <th>Fastest Lap Time</th>
                    </tr>
                  </thead>
                  <tbody id="track2-table-body">
                    {track2.map((item, index) => (
                      <tr key={index}>
                        <td>{item.name}</td>
                        <td>{item.car}</td>
                        <td>{item.time}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          {/* Display track 3 data */}
          <div className="leaderboard-item">
            <div className="row">
              <div className="col-xl">
                <h3 className="text-left">Rainbow Road</h3>
                <table className="table table-striped text-left">
                  <thead>
                    <tr>
                      <th>Driver Name</th>
                      <th>Car</th>
                      <th>Fastest Lap Time</th>
                    </tr>
                  </thead>
                  <tbody id="track3-table-body">
                    {track3.map((item, index) => (
                      <tr key={index}>
                        <td>{item.name}</td>
                        <td>{item.car}</td>
                        <td>{item.time}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <img src="assets/track3.jpg" alt="Rainbow Road" />
            </div>
          </div>

          {/* Display track 4 data */}
          <div className="leaderboard-item">
            <div className="row">
              <img src="assets/track4.jpg" alt="Hyrule Circuit" />
              <div className="col-xl">
                <h3 className="text-left">Hyrule Circuit</h3>
                <table className="table table-striped text-left">
                  <thead>
                    <tr>
                      <th>Driver Name</th>
                      <th>Car</th>
                      <th>Fastest Lap Time</th>
                    </tr>
                  </thead>
                  <tbody id="track4-table-body">
                    {track4.map((item, index) => (
                      <tr key={index}>
                        <td>{item.name}</td>
                        <td>{item.car}</td>
                        <td>{item.time}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Leaderboards;
