import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const BookingForm = () => {
  const [selectedVehicle, setSelectedVehicle] = useState('');
  const [selectedTrack, setSelectedTrack] = useState('');
  const [selectedInstructor, setSelectedInstructor] = useState('');
  const [datetime, setDatetime] = useState('');

  const handleVehicleChange = (event) => {
    setSelectedVehicle(event.target.value);
  };

  const handleTrackChange = (event) => {
    setSelectedTrack(event.target.value);
  };

  const handleInstructorChange = (event) => {
    setSelectedInstructor(event.target.value);
  };

  const handleDatetimeChange = (event) => {
    setDatetime(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Perform form submission or other actions here
    // For this example, we'll just log the selected values
    console.log('Name:', event.target.name.value);
    console.log('Email:', event.target.email.value);
    console.log('Vehicle:', selectedVehicle);
    console.log('Track:', selectedTrack);
    console.log('Instructor:', selectedInstructor);
    console.log('Date and Time:', datetime);

    // Clear the form fields
    event.target.name.value = '';
    event.target.email.value = '';
    setSelectedVehicle('');
    setSelectedTrack('');
    setSelectedInstructor('');
    setDatetime('');
  };

  useEffect(() => {
    // Set minimum date to today in the date-time picker
    const today = new Date().toISOString().split('T')[0];
    document.getElementById('datetime').min = today;

    // Pre-select the vehicle in the form if it exists
    const urlParams = new URLSearchParams(window.location.search);
    const vehicle = urlParams.get('vehicle');
    if (vehicle) {
      setSelectedVehicle(vehicle);
    }
  }, []);

  return (
    <div>
    

      {/* Booking Section */}
      <section id="booking" className="py-5">
        <div className="container">
          <h2 className="display-4 text-center mb-5">Book Now</h2>
          <form onSubmit={handleSubmit} id="bookingForm">
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor="name">Name</label>
                <input type="text" className="form-control" id="name" placeholder="Enter your name" required />
              </div>
              <div className="form-group col-md-6">
                <label htmlFor="email">Email address</label>
                <input type="email" className="form-control" id="email" placeholder="Enter your email" required />
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor="vehicle ">Vehicle</label>
                <select
                  id="vehicle"
                  className="form-control"
                  required
                  value={selectedVehicle}
                  onChange={handleVehicleChange}
                >
                  <option value="" disabled>Select your vehicle</option>
                  <option value="Personal vehicle">Personal vehicle</option>
                  <option value="Porsche 911 GT3 RS">Porsche 911 GT3 RS</option>
                  <option value="BMW M4 comp.">BMW M4 comp.</option>
                  <option value="Audi RS5">Audi RS5</option>
                  <option value="Mercedes C63s AMG">Mercedes C63s AMG</option>
                  <option value="Nissan Nismo GT-R">Nissan Nismo GT-R</option>
                  {/* Add more vehicle options as needed */}
                </select>
              </div>
              <div className="form-group col-md-6">
                <label htmlFor="track">Track</label>
                <select
                  id="track"
                  className="form-control"
                  required
                  value={selectedTrack}
                  onChange={handleTrackChange}
                >
                  <option value="" disabled>Select a Track</option>
                  <option value="Daisy Circuit">Daisy Circuit</option>
                  <option value="Royal Raceway">Royal Raceway</option>
                  <option value="Rainbow Road">Rainbow Road</option>
                  <option value="Hyrule Circuit">Hyrule Circuit</option>
                </select>
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-6">
                <label htmlFor="expert">Instructor</label>
                <select
                  id="expert"
                  className="form-control"
                  required
                  value={selectedInstructor}
                  onChange={handleInstructorChange}
                >
                  <option value="" disabled>Select an instructor</option>
                  <option value="No instructor">No instructor</option>
                  <option value="Max">Max</option>
                  <option value="Chris">Chris</option>
                </select>
              </div>
              <div className="form-group col-md-6">
                <label htmlFor="datetime">Date and Time</label>
                <input
                  type="datetime-local"
                  className="form-control"
                  id="datetime"
                  required
                  value={datetime}
                  onChange={handleDatetimeChange}
                />
              </div>
            </div>
            <button type="submit" className="btn btn-danger btn-block">Submit</button>
          </form>
        </div>
      </section>

      {/* Booking Confirmation Modal */}
      <div className="modal fade" id="bookingModal" tabIndex="-1" role="dialog" aria-labelledby="bookingModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="bookingModalLabel">Booking Confirmation</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <p><strong>Your form has been submitted! Our team will be in touch with you.</strong></p>
              <p><strong>Name:</strong> <span id="modalName"></span></p>
              <p><strong>Email:</strong> <span id="modalEmail"></span></p>
              <p><strong>Vehicle:</strong> <span id="modalVehicle"></span></p>
              <p><strong>Track:</strong> <span id="modalTrack"></span></p>
              <p><strong>Instructor:</strong> <span id="modalExpert"></span></p>
              <p><strong>Date and Time:</strong> <span id="modalDateTime"></span></p>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
              <button type="button" className="btn btn-danger" id="editButton">Edit</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BookingForm;