import React, { useState } from 'react';
import './cars.css';
import Card from 'react-bootstrap/Card';

const Cars = () => {
  const carData = [
    {
      id: 1,
      brand: 'Porsche',
      name: 'Porsche 911 GT3 RS',
      description: 'Description of Car 1.',
      price: 4000,
      image: 'assets/car1.jpg',
    },
    {
      id: 2,
      brand: 'BMW',
      name: 'BMW M4 comp.',
      description: 'Description of Car 2.',
      price: 3000,
      image: 'assets/car2.jpg',
    },
    {
      id: 3,
      brand: 'Audi',
      name: 'Audi RS5',
      description: 'Description of Car 3.',
      price: 3000,
      image: 'assets/car3.jpg',
    },
    {
      id: 4,
      brand: 'Mercedes',
      name: 'Mercedes C63s AMG',
      description: 'Description of Car 4.',
      price: 2500,
      image: 'assets/main.jpg',
    },
    {
      id: 5,
      brand: 'Nissan',
      name: 'Nissan Nismo GT-R',
      description: 'Description of Car 5.',
      price: 3500,
      image: 'assets/car5.jpg',
    },
  ];

  const [selectedBrands, setSelectedBrands] = useState([]);
  const [sortBy, setSortBy] = useState('asc');

  const handleBrandChange = (e) => {
    const brand = e.target.value;
    if (selectedBrands.includes(brand)) {
      setSelectedBrands(selectedBrands.filter((item) => item !== brand));
    } else {
      setSelectedBrands([...selectedBrands, brand]);
    }
  };

  const handleSortChange = (e) => {
    setSortBy(e.target.value);
  };

  const filterCars = (cars, selectedBrands) => {
    return selectedBrands.length > 0
      ? cars.filter((car) => selectedBrands.includes(car.brand))
      : cars;
  };

  const sortCars = (cars, sortBy) => {
    return cars.sort((a, b) => {
      return sortBy === 'asc' ? a.price - b.price : b.price - a.price;
    });
  };

  const filteredAndSortedCars = sortCars(filterCars(carData, selectedBrands), sortBy);

  return (
    <section id="car-selection" className="py-5">
      <div className="container">
        <h2 className="display-4 text-center mb-5">Car Rentals</h2>
        <div className="row">
          <div className="col-lg-3 text-left">
            <div className="filter-section">
              <h5>Brands</h5>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Audi"
                    checked={selectedBrands.includes('Audi')}
                    onChange={handleBrandChange}
                  />
                   Audi
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="BMW"
                    checked={selectedBrands.includes('BMW')}
                    onChange={handleBrandChange}
                  />
                  BMW
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Mercedes"
                    checked={selectedBrands.includes('Mercedes')}
                    onChange={handleBrandChange}
                  />
                  Mercedes
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Nissan"
                    checked={selectedBrands.includes('Nissan')}
                    onChange={handleBrandChange}
                  />
                  Nissan
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Porsche"
                    checked={selectedBrands.includes('Porsche')}
                    onChange={handleBrandChange}
                  />
                  Porsche
                </label>
              </div>
            </div>
            <div className="sort-section">
              <h5>Sort By Price</h5>
              <select id="sort-price" className="form-control" value={sortBy} onChange={handleSortChange}>
                <option value="asc">Low to High</option>
                <option value="desc">High to Low</option>
              </select>
            </div>
          </div>
          <div className="col-lg-9">
            <div className="car-list">
              {filteredAndSortedCars.map((car) => (
                <div className="car-item" data-brand={car.brand} key={car.id}>
                  


                  <Card style={{ width: '18rem' }}>
                    <Card.Img variant="bottom" src={car.image} alt={car.name} />
                    <Card.Body>
                      <h5 className="card-title text-left">{car.name}</h5>
                      <p className="card-text text-left">{car.description}</p>
                      <p className="car-price text-left">${car.price}/day</p>
                      <a href={`booking?vehicle=${encodeURIComponent(car.name)}`} className="btn btn-danger ">
                        Book Now
                      </a>
                    </Card.Body>
                  </Card>


                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Cars;
