import React, { useState } from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

const Header = () => {
  const [isFrench, setIsFrench] = useState(false);

  const toggleLanguage = () => {
    setIsFrench((prevIsFrench) => !prevIsFrench);
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-dark" style={{ backgroundColor: '#c0c0c0' }}>
      <a className="navbar-brand" href="/">
        <img src="assets/logo1.png" alt="Logo" width="180" height="80" className="d-inline-block align-top" />
      </a>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarNav">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <a className="nav-link" href="/" style={{ color: 'black' }}>
              {isFrench ? 'Accueil' : 'Home'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="tracks" style={{ color: 'black' }}>
              {isFrench ? 'Pistes' : 'Tracks'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="cars" style={{ color: 'black' }}>
              {isFrench ? 'Voitures' : 'Cars'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="instructors" style={{ color: 'black' }}>
              {isFrench ? 'Instructeurs' : 'Instructors'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="leaderboards" style={{ color: 'black' }}>
              {isFrench ? 'Classements' : 'Leaderboards'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="contact" style={{ color: 'black' }}>
              {isFrench ? 'Contactez-nous' : 'Contact Us'}
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="booking" style={{ color: 'black' }}>
              {isFrench ? 'Réserver maintenant' : 'Book Now'}
            </a>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Header;
