import React, { useState } from 'react';
import './App.css'
import { HashRouter as Router, Route, Routes } from 'react-router-dom';
import Home from './Home';
import Leaderboards from './leaderboards';
import Header from './header';
import Contact from './contact';
import Booking from './booking';
import Tracks from './tracks';
import Instructors from './instructors';
import Cars from './cars';

function App() {

  const [isFrench, setIsFrench] = useState(false);

  const toggleLanguage = () => {
    setIsFrench((prevIsFrench) => !prevIsFrench);
  };

  return (
    <Router>
      <div className="App">
        <div className="content">
        <Header isFrench={isFrench} toggleLanguage={toggleLanguage}/>
          <Routes>

          <Route exact path="/Project2" element={<Home />} />


          <Route exact path="/leaderboards" element={<Leaderboards />} />

          <Route exact path="/contact" element={<Contact />} />

          <Route exact path="/booking" element={<Booking />} />

          <Route exact path="/tracks" element={<Tracks />} />

          <Route exact path="/instructors" element={<Instructors />} />

          <Route exact path="/cars" element={<Cars />} />

          </Routes>
        </div>
      </div>
    </Router>  
  );
}

export default App;


