import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './tracks.css';
import Card from 'react-bootstrap/Card';

const OurTracks = () => {
  const [selectedDifficulties, setSelectedDifficulties] = useState([]);
  const [sortBy, setSortBy] = useState('asc');

  const trackData = [
    // ... track data objects ...
    {
        name: 'Daisy Circuit',
        description: 'Description of Daisy Circuit.',
        length: 2.5,
        difficulty: 'Easy',
        image: 'assets/track1.jpg',
        modalImage: 'assets/track-pic1.jpg',
      },
      {
        name: 'Royal Raceway',
        description: 'Description of Royal Raceway.',
        length: 3.2,
        difficulty: 'Medium',
        image: 'assets/track2.jpg',
        modalImage: 'assets/track-pic2.jpg',
      },
      {
        name: 'Rainbow Road',
        description: 'Description of Rainbow Road.',
        length: 4.7,
        difficulty: 'Hard',
        image: 'assets/track3.jpg',
        modalImage: 'assets/track-pic3.jpg',
      },
      {
        name: 'Hyrule Circuit',
        description: 'Description of Hyrule Circuit.',
        length: 3.1,
        difficulty: 'Medium',
        image: 'assets/track4.jpg',
        modalImage: 'assets/track-pic4.jpg',
      },
  ];

  const handleDifficultyChange = (e) => {
    const difficulty = e.target.value;
    setSelectedDifficulties((prev) =>
      prev.includes(difficulty) ? prev.filter((item) => item !== difficulty) : [...prev, difficulty]
    );
  };

  const handleSortChange = (e) => {
    setSortBy(e.target.value);
  };

  const filterTracks = (tracks, selectedDifficulties) => {
    return selectedDifficulties.length > 0
      ? tracks.filter((track) => selectedDifficulties.includes(track.difficulty))
      : tracks;
  };

  const sortTracks = (tracks, sortBy) => {
    return tracks.sort((a, b) => {
      return sortBy === 'asc' ? a.length - b.length : b.length - a.length;
    });
  };

  const filteredAndSortedTracks = sortTracks(filterTracks(trackData, selectedDifficulties), sortBy);

  return (
    <section id="track-selection" className="py-5">
      <div className="container">
        <h2 className="display-4 text-center mb-5">Our Racetracks</h2>

        <div className="row">
          <div className="col-lg-3 text-left">
            <div className="filter-section">
              <h5>Difficulty</h5>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Easy"
                    checked={selectedDifficulties.includes('Easy')}
                    onChange={handleDifficultyChange}
                  />
                  Easy
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Medium"
                    checked={selectedDifficulties.includes('Medium')}
                    onChange={handleDifficultyChange}
                  />
                  Medium
                </label>
              </div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    value="Hard"
                    checked={selectedDifficulties.includes('Hard')}
                    onChange={handleDifficultyChange}
                  />
                  Hard
                </label>
              </div>
            </div>

            <div className="sort-section">
              <h5>Sort By Length</h5>
              <select
                id="sort-length"
                className="form-control"
                value={sortBy}
                onChange={handleSortChange}
              >
                <option value="asc">Short to Long</option>
                <option value="desc">Long to Short</option>
              </select>
            </div>
          </div>

          <div className="col-lg-9">
            <div className="track-list">
              {filteredAndSortedTracks.map((track) => (
                <div className="track-item text-left" key={track.name} data-difficulty={track.difficulty}>
                  <Card style={{ width: '18rem' }}>
                    <Card.Img variant="bottom" src={track.image} alt={track.name} />
                    <Card.Body>
                      <h5 className="card-title">{track.name}</h5>
                      <p className="card-text">{track.description}</p>
                      <p className="track-length">Length: {track.length} miles</p>
                      <button
                        type="button"
                        className="btn btn-danger"
                        data-toggle="modal"
                        data-target={`#trackModal${track.name.replace(/\s+/g, '')}`}
                      >
                        View
                      </button>
                    </Card.Body>
                  </Card>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OurTracks;
